.menu {
  background: linear-gradient(135deg, #ffecd2, #fcb69f, #ffdde1) fixed;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 2.5rem;
  animation: 1s ease-in-out fadeIn;
  display: flex;
}

.menu > div:first-child {
  text-align: center;
  -webkit-backdrop-filter: blur(14px) saturate(180%);
  background: #fff9;
  border: 1.5px solid #fff6;
  border-radius: 1.8rem;
  width: 100%;
  max-width: 45rem;
  margin-bottom: 2.5rem;
  padding: 3rem;
  transition: transform .4s, box-shadow .4s;
  animation: .9s slideUp;
  box-shadow: 0 12px 32px #0003;
}

.menu > div:first-child:hover {
  transform: translateY(-8px)scale(1.03);
  box-shadow: 0 16px 40px #ff57224d;
}

.menu h1 {
  -webkit-text-fill-color: transparent;
  letter-spacing: 1px;
  text-shadow: 0 2px 6px #ff572233;
  background: linear-gradient(90deg, #ff512f, #dd2476, #ff8a50);
  -webkit-background-clip: text;
  margin-bottom: .8rem;
  font-size: 3rem;
  font-weight: 900;
}

.menu p {
  color: #374151;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.7;
}

.restaurant-info {
  justify-content: center;
  align-items: center;
  gap: 2.2rem;
  margin-top: 1.5rem;
  display: flex;
}

.restaurant-info h2 {
  color: #374151;
  align-items: center;
  gap: .5rem;
  font-size: 1.3rem;
  font-weight: 700;
  display: flex;
  position: relative;
}

.restaurant-info h2:first-child {
  color: #22c55e;
  text-shadow: 0 0 10px #22c55e80;
  animation: 2s infinite pulseGlow;
}

.menu-list {
  backdrop-filter: blur(10px) saturate(150%);
  background: #ffffffb3;
  border: 1.5px solid #ffffff4d;
  border-radius: 1.6rem;
  width: 100%;
  max-width: 45rem;
  padding: 2.8rem;
  animation: 1.1s fadeInUp;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 28px #00000026;
}

.menu-list:before {
  content: "";
  z-index: -1;
  opacity: .4;
  filter: blur(20px);
  background: linear-gradient(45deg, #ff512f, #ff8a50, #dd2476, #ff512f);
  border-radius: 1.8rem;
  animation: 6s linear infinite borderGlow;
  position: absolute;
  inset: -3px;
}

.menu-list h2 {
  color: #1f2937;
  text-align: center;
  text-shadow: 0 2px 8px #ff572226;
  margin-bottom: 1.5rem;
  font-size: 2.2rem;
  font-weight: 800;
  position: relative;
}

.menu-list h2:after {
  content: "";
  background: linear-gradient(90deg, #ff5722, #ff8a50);
  border-radius: 2px;
  width: 70px;
  height: 4px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 12px #ff572299;
}

.menu-list p {
  color: #4b5563;
  text-align: center;
  font-size: 1.1rem;
  line-height: 1.6;
  animation: 1.2s fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px)scale(.95);
  }

  to {
    opacity: 1;
    transform: translateY(0)scale(1);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: .85;
    transform: scale(1.08);
  }
}

@keyframes borderGlow {
  0% {
    filter: blur(20px) hue-rotate();
  }

  100% {
    filter: blur(20px) hue-rotate(360deg);
  }
}

@media (width <= 768px) {
  .menu {
    padding: 1rem;
  }

  .menu > div:first-child, .menu-list {
    border-radius: 1.2rem;
    padding: 1.8rem;
  }

  .menu h1 {
    font-size: 2.2rem;
  }

  .restaurant-info {
    flex-direction: column;
    gap: 1rem;
  }

  .menu-list h2 {
    font-size: 1.8rem;
  }
}
